import ManufacturingOverviewRow from "./manufacturing_overview_row.vue";

export default {
  components: {
    ManufacturingOverviewRow,
  },
  data() {
    return {
      salesorderData: [
        {
          customer: "",
          delivery_date: "",
          link: "",
          name: "",
          item_name: "Loading...",
          item_code: "",
          qty: "",
          sales_order: "",
          status: "Unknown",
          due_in: 0,
        },
      ],
      timer: "",
      origin: window.location.origin,
      userPermissions: {},
    };
  },
  created() {
    this.fetchEventsList();
    this.timer = setInterval(this.fetchEventsList, 30000);
  },
  methods: {
    fetchEventsList() {
      let self = this;
      frappe.call({
        method:
          "manufacturing_overview.manufacturing_overview.api.getSalesorderOverviewList",
        async: true,
        args: {},
        callback: function (r) {
          if (r.message) {
            self.salesorderData = r.message;
          }
        },
      });
    },
    cancelAutoUpdate() {
      clearInterval(this.timer);
    },
    beforeDestroy() {
      clearInterval(this.timer);
    },
  },
};
